.summary-button {
  font-size: 1rem;
  font-weight: bold;
  letter-spacing: 0.1em;
  padding: 5px 10px;
  margin-left: 15px;
  border-radius: 5px;
}
.tempMax, .tempMin, .humidity, .wind {
  display: flex;
  justify-content: center;
  font-weight: lighter;
  font-style: italic;
  font-family: sans-serif;
  font-size: 1rem;
  letter-spacing: 0.02em;
  justify-content: center;

  background: black;
      color: white;
      padding: 80px;
      text-shadow:
                  1px 1px 0px gold,
                  -3px -0 25px red,
                  3px 0px 3px white,
                  0px 0px 15px white,
                  15px 10px 10px black,
                  0 0 40px orangered,
                  0 0 40px orangered,
                  0 0 60px orangered,
                  0 0 80px orangered,
                  0 0 80px orangered,
                  0 0 100px orangered,
                  0 0 150px orangered;
}

.date-more {
  display: flex;
  justify-content: center;
}