@media screen and (max-width: 768px) {
  .forecast-summaries {
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
  }
}
.forecast-summaries {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 1.5rem;
  letter-spacing: 0.02em;
  padding: 0 0 30px 0;
}
